/**
 * External dependencies
 */
import { registerCheckoutBlock } from '@woocommerce/blocks-checkout';
import { useState } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { ValidatedTextInput, Panel, TotalsWrapper, Button, TotalsItem } from "@woocommerce/blocks-components";
import { Spinner } from '@wordpress/components'; 
import { useSelect, useDispatch } from '@wordpress/data';

/**
 * Internal dependencies
 */
import metadata from './block.json';


const FrontendBlock = ( { children, checkoutExtensionData } ) => {
	const [ giftCardCode, setGiftCardCode ] = useState('');
	const [ submittingCode, setSubmittingCode ] = useState(false);
	const [error, setError] = useState('');
	const { extensionCartUpdate } = window.wc.blocksCheckout;

	const { setValidationErrors, clearValidationError } = useDispatch(
		'wc/store/validation'
	);

	const cartData = useSelect((select) => {
		const store = select('wc/store/cart');
		return store.getCartData();
	})

	let total = Number(cartData.totals.total_price);
	let giftupData = cartData.extensions['giftup-apply-block'];

	let appliedBalance = 0;

	if(giftupData){
		appliedBalance = giftupData.giftcard_applied_balance * 100;

		if (submittingCode && giftCardCode && giftupData.giftcard_not_found) {
			// show error
			setError(__('Gift card not found', 'gift-up'));
			setSubmittingCode(false);
		}
	}

	const applyCode = () => {
		giftupData.giftcard_code = null;
		giftupData.giftcard_not_found = null;

		let data = {
			giftCardCode
		};

		setError('');
		setSubmittingCode(true);

		extensionCartUpdate({
			'namespace': 'giftup-apply-code',
			data
		});
	}

	const removeCode = () => {
		extensionCartUpdate({
			'namespace': 'giftup-remove-code'
		});

		setError('');
		setGiftCardCode('');
		setSubmittingCode(false);
	}

	const handleKeyPress = (event) => {
		setError('');
		if (event.key === 'Enter') {
			applyCode();
		}
	};

	let errorContent;

	if (error) {
		errorContent =
			<div className='wc-block-components-validation-error giftup-apply-error'><svg xmlns="http://www.w3.org/2000/svg" viewBox="-2 -2 24 24" width="24" height="24" aria-hidden="true" focusable="false"><path d="M10 2c4.42 0 8 3.58 8 8s-3.58 8-8 8-8-3.58-8-8 3.58-8 8-8zm1.13 9.38l.35-6.46H8.52l.35 6.46h2.26zm-.09 3.36c.24-.23.37-.55.37-.96 0-.42-.12-.74-.36-.97s-.59-.35-1.06-.35-.82.12-1.07.35-.37.55-.37.97c0 .41.13.73.38.96.26.23.61.34 1.06.34s.8-.11 1.05-.34z"></path></svg> <span>{error}</span></div>
	}

	let buttonText;
	if (submittingCode) {
		buttonText = <Spinner />;
	} else {
		buttonText = <span className="wc-block-components-button__text">{__('Apply', 'gift-up')}</span>
	}

	let content;

	if (!(giftupData && giftupData.giftcard_code)) {
		content =
			<>
			<Panel
				className={`giftup-apply-panel wc-block-components-totals-coupon ${!!error ? 'has-error' : ''}`}
				title={__('Have a gift card?', 'gift-up')}
				titleTag="div"
			>
				<div className="giftup-apply-panel-form">
					<div className="giftup-apply-input">
						<ValidatedTextInput
							autoFocus
							id="giftcard-code"
							value={giftCardCode}
							onKeyPress={ handleKeyPress }
							label={__('Gift card code', 'gift-up')}
							customValidation={ () => !error}
							className={!!error ? 'has-error' : ''}
							onChange={(value) => setGiftCardCode(value)}
						/>
					</div>
					<Button
						className="giftup-apply-button"
						disabled={giftCardCode.length < 1}
						onClick={applyCode}
						type="button">
						{buttonText}
					</Button>
				</div>
				{errorContent}
			</Panel>
			</>

	} else {
		// content =
		// 	<>
		// 	<TotalsItem
		// 	value={total + appliedBalance}
		// 	label={__('Total', 'gift-up')}
		// 	description={__('(before gift cards)', 'gift-up')}
		// 	className="wc-block-components-totals-footer-item"
		// 	></TotalsItem>
		// 	</>;
	}

	content = <TotalsWrapper>{content}</TotalsWrapper>;

	let balanceContent;

	if (appliedBalance)
	{
		balanceContent =
			<TotalsWrapper className="giftup-apply-panel">
				<TotalsItem
					value={-appliedBalance}
					label={__('Gift card', 'gift-up') + ":"}
					className="wc-block-components-totals-discount"
					description={
					<>
					<div className="wc-block-components-chip wc-block-components-chip--radius-large">
						<span className="wc-block-components-chip__text">{giftupData.giftcard_code}</span>
						<button className="giftup-remove-button wc-block-components-chip__remove"
								onClick={removeCode}
								type="button">
							<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="16" height="16" className="wc-block-components-chip__remove-icon" aria-hidden="true" focusable="false"><path d="M12 13.06l3.712 3.713 1.061-1.06L13.061 12l3.712-3.712-1.06-1.06L12 10.938 8.288 7.227l-1.061 1.06L10.939 12l-3.712 3.712 1.06 1.061L12 13.061z"></path></svg>
						</button>
					</div>
					<div>
						<strong>{__('Available balance', 'gift-up')}:</strong> <span>{giftupData.giftcard_balance}</span>
					</div>
					</>
					}>
				</TotalsItem>

			</TotalsWrapper>
	}

	return (
		<>
			<style>
				{`
					.giftup-apply-panel {
						
						.wc-block-components-panel__content {
							.giftup-apply-panel-form {
								margin-bottom:0;
								padding-bottom: 0;
								display: flex;
								flex-wrap: wrap;
								gap: 8px;
								width: 100%;

								.giftup-apply-input{
									margin-top:0;
									flex: 3 1 140px;

									.wc-block-components-text-input {
										margin-top: 0;
									}
								}

								.giftup-apply-button {
									flex: 1 1 auto;
									padding-left: 16px;
									padding-right: 16px;
									white-space: nowrap;
								}
							}
						}

						.giftup-apply-error {
							align-items: center;
							display: flex;
							gap: 2px;
							line-height: 1;
							margin: 0;
						}

						.wc-block-components-chip__text {
							padding-right: 0.5em;
						}
					}
				`
				}
			</style>

			{content}
			{balanceContent}
		</>
	);
};

let x = registerCheckoutBlock({
	metadata: metadata,
	component: FrontendBlock
} );

